___CSS_LOADER_EXPORT___.push([module.id, `body {
    box-sizing: border-box;
    padding: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: antiquewhite;
}

div.card {
    border: 1px solid #ccc;
    border-radius: 8px;
    background-image: linear-gradient(120deg, #65f6d7ab 0%, #9f85fdb7 100%);
    width: 500px;
    margin-top: 4%;
    padding-bottom: 1em;
}

div.search-bar {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1em;
    gap: 1em;
}

#search-input {
    border-radius: 24px;
    font-size: 1.2em;
    padding: 0.8em;
    width: 60%;
    border: none;
}

#search-btn {
    border-radius: 50%;
    font-size: 1.2em;
    padding: 0.8em;
    background-color: #fff;
    border: none;
    color: #000;
    cursor: pointer;
    display: grid;
    place-items: center;
}

img.search-svg {
    width: 1em;
}

.error-message {
    color: rgb(226, 80, 80);
    font-size: 0.9em;
    text-align: center;
    margin: 0.3em 0;
    display: none;
}

div.info {
    padding: 1em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

div.info img[src\$=".svg"] {
    width: 20em;
}

div.info h1 {
    text-transform: capitalize;
    color: white;
    text-align: center;
    margin: 0 auto;
}

div.additional-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1em;
    color: white;
}

img.wind-svg.add-info-svg {
    width: 25%;
}

img.humidity-svg.add-info-svg {
    width: 30%;
}

div.humidity,
div.wind {
    display: flex;
    margin: 0 1em 0.5em;
}

div.wind {
    justify-content: flex-end;
    margin-right: 2em;
}

div.add-info {
    display: flex;
    flex-direction: column;
}

div.add-info p {
    margin: 0;
    text-align: center;
    font-size: 1.2em;
}

div.units img[src\$=".svg"] {
    width: 100%;
}

div.units button {
    display: inline-grid;
    place-content: center;
    border: none;
    background-color: #fff;
    padding: 0;
    border-radius: 8px;
    cursor: pointer;
    font-size: 1.2em;
    margin: 0.5em;
    width: 2em;
}

div.units button.active {
    background-color: #00f20060;
}

footer {
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    text-align: center;
    padding: 1em;
    margin-bottom: 1em;
}

footer p {
    margin: 0;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,UAAU;IACV,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,mJAAmJ;IACnJ,8BAA8B;AAClC;;AAEA;IACI,sBAAsB;IACtB,kBAAkB;IAClB,uEAAuE;IACvE,YAAY;IACZ,cAAc;IACd,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,YAAY;IACZ,QAAQ;AACZ;;AAEA;IACI,mBAAmB;IACnB,gBAAgB;IAChB,cAAc;IACd,UAAU;IACV,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,cAAc;IACd,sBAAsB;IACtB,YAAY;IACZ,WAAW;IACX,eAAe;IACf,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,uBAAuB;IACvB,gBAAgB;IAChB,kBAAkB;IAClB,eAAe;IACf,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,0BAA0B;IAC1B,YAAY;IACZ,kBAAkB;IAClB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,UAAU;AACd;;AAEA;;IAEI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,SAAS;IACT,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,oBAAoB;IACpB,qBAAqB;IACrB,YAAY;IACZ,sBAAsB;IACtB,UAAU;IACV,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,aAAa;IACb,UAAU;AACd;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,WAAW;IACX,eAAe;IACf,SAAS;IACT,OAAO;IACP,kBAAkB;IAClB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,SAAS;AACb","sourcesContent":["body {\r\n    box-sizing: border-box;\r\n    padding: 0;\r\n    height: 100%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    align-items: center;\r\n    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\r\n    background-color: antiquewhite;\r\n}\r\n\r\ndiv.card {\r\n    border: 1px solid #ccc;\r\n    border-radius: 8px;\r\n    background-image: linear-gradient(120deg, #65f6d7ab 0%, #9f85fdb7 100%);\r\n    width: 500px;\r\n    margin-top: 4%;\r\n    padding-bottom: 1em;\r\n}\r\n\r\ndiv.search-bar {\r\n    display: f